import React from 'react';
import { QuotesResponse } from '../../services/quoteservice';
import { UserObj } from '../../types/types';

const PencilComponent = ({
  row,
  currentUser,
  modifyInterest,
}: {
  row: QuotesResponse;
  currentUser: Pick<UserObj, 'id' | 'fullname' | 'phone'> | undefined;
  modifyInterest: (rowId: number, rowInterested: number) => void;
}) => {
  let pencil: React.ReactNode;
  if (row.user.id == currentUser?.id) {
    pencil = (
      <i
        onClick={() => modifyInterest(row.id!, row.interested)}
        style={{ cursor: 'pointer' }}
        className="fa-solid fa-pencil ms-2"
      />
    );
  } else {
    pencil = null;
  }
  if (row.interested === 1) {
    return (
      <>
        <i className="fa-solid fa-circle text-danger" />
        {pencil}
      </>
    );
  }
  if (row.interested === 2) {
    return (
      <>
        <i className="fa-solid fa-circle text-warning" /> {pencil}
      </>
    );
  }
  if (row.interested === 3) {
    return (
      <>
        <i className="fa-solid fa-circle text-success" /> {pencil}
      </>
    );
  }
  return <i className="fa-solid fa-circle text-secondary" />;
};

export default PencilComponent;
