import {
  CustomerObj,
  DptoObj,
  GarageObj,
  QuoteObj,
  ServiceObj,
  TypologyObj,
  UserObj,
  BuildingObj,
} from '../types/types';
import { CommonResponse } from './buildingservice';
import { client } from '../httpClient';

export interface QuotesResponse extends QuoteObj {
  dpto: DptoObj & { typology: TypologyObj; building: BuildingObj };
  garage: GarageObj;
  customer: CustomerObj;
  user: Pick<UserObj, 'id' | 'fullname' | 'phone'>;
}

export interface QuoteResponse extends QuotesResponse {
  dpto: DptoObj & {
    typology: TypologyObj;
    building: BuildingObj & { services: ServiceObj[] };
  };
}

export const postQuote = async (objQuotePost: {
  quote: QuoteObj;
  customer: CustomerObj;
}) => {
  try {
    const response = await client.post(`/quote`, objQuotePost);
    return response.data as CommonResponse<QuoteObj>;
  } catch (err) {
    return err.response.data;
  }
};

export const getQuotes = async () => {
  try {
    const response = await client.get(`/quote`);
    return response.data as CommonResponse<QuotesResponse[]>;
  } catch (error) {}
};

export const getQuoteById = async (id: number) => {
  try {
    const response = await client.get(`/quote/${id}`);
    return response.data as CommonResponse<QuoteResponse>;
  } catch (error) {}
};

export const putQuoteInterestByQuoteId = async (
  quoteId: number,
  interest: number
) => {
  try {
    const response = await client.put(`/quote/update-interest/${quoteId}`, {
      interest,
    });
    return response.data as CommonResponse<string>;
  } catch (error) {}
};
