import React, { useContext, useEffect, useState } from 'react';
import DataTable, { TableColumn } from 'react-data-table-component';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
  getQuotes,
  putQuoteInterestByQuoteId,
  QuotesResponse,
} from '../../services/quoteservice';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { AuthContextQuote } from '../../authQuote/context/context';
import GenericModal from '../../shared/components/GenericModal';
import { InterestButton } from '../../shared/components/InterestButton';
import { RoutesCollection } from '../../routes/contract';
import PencilComponent from '../components/PencilComponent';
import FilterComponent from '../components/FilterComponent';

interface InterestInterface {
  quoteId: number;
  interested: number;
}

const PageQuoteList = () => {
  const AuthContext = useContext(AuthContextQuote);
  const currentUser = AuthContext?.currentUser;
  const [showInterestModal, setShowInterestModal] = useState(false);
  const [interestValues, setInterestValues] = useState<InterestInterface>({
    quoteId: 0,
    interested: 0,
  });

  const { quoteId, interested } = interestValues;
  const [quotes, setQuotes] = useState<QuotesResponse[]>();
  const [loadingQuotes, setLoadingQuotes] = useState(true);
  const navigate = useNavigate();

  const modifyInterest = async (quoteId: number, interested: number) => {
    setInterestValues({ quoteId, interested });
    setShowInterestModal(true);
  };

  const saveInterest = async (interest: number) => {
    const response = await putQuoteInterestByQuoteId(quoteId, interest);
    if (response.ok) {
      setShowInterestModal(false);
      toast('Nivel de inters guardado exitosamente', {
        type: 'success',
      });
      const newQuotes = quotes?.map((quote) => {
        if (quote.id === quoteId) quote.interested = interest;
        return quote;
      });
      setQuotes(newQuotes);
    } else {
      toast('No se pudo cambiar el nivel de inters', {
        type: 'error',
      });
    }
  };

  const getQuotesCall = async () => {
    try {
      setLoadingQuotes(true);
      const response = await getQuotes();
      if (response.ok) {
        const orderedQuotes = response.content.sort(
          (obj1, obj2) => obj2.id! - obj1.id!
        );
        setQuotes(orderedQuotes);
      }
      setLoadingQuotes(false);
    } catch (error) {}
  };
  useEffect(() => {
    getQuotesCall();
  }, []);

  const columns: TableColumn<QuotesResponse>[] = [
    {
      name: 'Vendedor',
      sortable: true,
      selector: (row: QuotesResponse) => row.user.fullname || '',
    },
    {
      name: 'Cliente',
      sortable: true,
      selector: (row: QuotesResponse) => row.customer.fullname || '',
    },
    {
      name: 'Proyecto',
      selector: (row: QuotesResponse) => row.dpto.building.name,
    },
    {
      name: 'Dpto',
      selector: (row: QuotesResponse) => row.dpto.number,
    },
    {
      name: 'Tipologa',
      selector: (row: QuotesResponse) => row.dpto.typology.name,
    },
    {
      name: 'Inters',
      // eslint-disable-next-line react/no-unstable-nested-components
      cell: (row: QuotesResponse) => (
        <PencilComponent
          row={row}
          currentUser={currentUser}
          modifyInterest={modifyInterest}
        />
      ),
    },
    {
      sortable: true,
      name: 'Fecha de cotizacin',
      selector: (row: QuotesResponse) => row.date.toString().split('T')[0],
    },
    {
      name: 'Acciones',
      // eslint-disable-next-line react/no-unstable-nested-components
      cell: (row: QuotesResponse) => (
        <button
          type="button"
          className="btn btn-primary"
          onClick={() =>
            navigate(RoutesCollection.goToQuoteSummary({ quote_id: row.id! }))
          }
        >
          <i className="fa-solid fa-eye" /> Ver
        </button>
      ),
      button: true,
    },
  ];

  const [filterText, setFilterText] = React.useState('');
  const [resetPaginationToggle, setResetPaginationToggle] =
    React.useState(false);
  const filteredItems = quotes
    ? quotes.filter(
        (item) =>
          (item.customer.fullname &&
            item.customer.fullname
              .toLowerCase()
              .includes(filterText.toLowerCase())) ||
          (item.user &&
            item.user.fullname.toLowerCase().includes(filterText.toLowerCase()))
      )
    : [];

  const subHeaderComponentMemo = React.useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText('');
      }
    };

    return (
      <FilterComponent
        onFilter={(e) => setFilterText(e.target.value)}
        onClear={handleClear}
        filterText={filterText}
      />
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <div className="container-fluid">
      <GenericModal
        show={showInterestModal}
        setShow={setShowInterestModal}
        title="Modificar inters"
      >
        <div className="row">
          <div className="col d-flex gap-4 text-center justify-content-center">
            <InterestButton
              interested={interested}
              saveInterest={saveInterest}
            />
          </div>
        </div>
      </GenericModal>
      <div className="row">
        <div className="col">
          <h1 className="display-6">Lista de cotizaciones</h1>
        </div>
      </div>
      <div className="row mt-3">
        <div className="col">
          <div className="card shadow">
            <div className="card-body">
              {loadingQuotes ? (
                <LoadingComponent
                  title="Cargando cotizaciones"
                  message="Conectndose a la base de datos de ngeles"
                />
              ) : (
                <DataTable
                  title="Cotizaciones"
                  pagination
                  columns={columns}
                  data={filteredItems!}
                  subHeader
                  subHeaderComponent={subHeaderComponentMemo}
                  persistTableHead
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PageQuoteList;
