import React from 'react';
import Step from './Step';

const Stepper = ({ ...rest }) => {
  const { currentStep, totalSteps } = rest;

  return (
    <div
      className="row px-4 mb-4 align-items-center"
      style={{ minHeight: '150px' }}
    >
      {Array(totalSteps)
        .fill('')
        .map((_, i) => (
          <Step
            key={i}
            currentStep={currentStep}
            totalSteps={totalSteps}
            step={i + 1}
          />
        ))}
    </div>
  );
};

export default Stepper;
