import { useNavigate, useParams } from 'react-router-dom';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import ReactToPrint from 'react-to-print';
import { toast } from 'react-toastify';
import NumberFormat from 'react-number-format';
import { getQuoteById, QuoteResponse } from '../../services/quoteservice';
import { getFormatedDateByStringDate, getFormatedMonth } from '../../utils';
import logoAngeles from '../../assets/logo.png';
import LoadingComponent from '../../shared/components/LoadingComponent';
import AngelesSorteoApolo from '../../assets/sorteo-auto-apolo.jpg';
import AngelesSorteoBolo2 from '../../assets/sorteo-auto-bolo2.jpg';
import { nameNumber } from '../../constants';
import {
  BuildingsResponse,
  getBuildingById,
} from '../../services/buildingservice';
import { isMobile } from '../../utils/mobile';
import { routesCollection } from '../../routes/contract';

const PageQuoteSummary = () => {
  const mobile = isMobile();
  const [loadingQuoteSummary, setLoadingQuoteSummary] = useState(false);
  const [quote, setQuote] = useState<QuoteResponse>();
  const [selectedBuilding, setSelectedBuilding] = useState<
    BuildingsResponse | undefined
  >(undefined);

  const { quote_id } = useParams();
  const navigate = useNavigate();

  const getQuoteByIdCaller = async (quote_id: number) => {
    setLoadingQuoteSummary(true);
    try {
      const responseQuote = await getQuoteById(quote_id);
      if (responseQuote.ok) {
        const responseBuilding = await getBuildingById(
          +responseQuote.content.dpto.building.id!
        );
        if (responseBuilding.ok) {
          setQuote(responseQuote.content);
          setSelectedBuilding(responseBuilding.content);
        }
      }
      setLoadingQuoteSummary(false);
    } catch (error) {
      toast('Hubo un error al intentar obtener los datos de la cotizacin', {
        type: 'error',
      });
      navigate(routesCollection.QUOTE_LIST);
    }
  };

  useEffect(() => {
    getQuoteByIdCaller(+quote_id!);
  }, []);

  const summaryQuote = { ...quote };
  const {
    garage: selectedGarage,
    initialAmount: initialCash = 0,
    paymentsQTY = 2,
    firstPaymentDate = '',
    discount = 0,
    lastPaymentDate = '',
    user,
    customer,
    type,
    garage_id,
    dptoPrice,
    dpto: selectedDpto,
  } = summaryQuote;
  const isCash = type === 'contado';

  const showGarages = selectedGarage && garage_id;

  const quoteRef = useRef(null);

  const totalAmount = useMemo(() => {
    if (showGarages) {
      return (dptoPrice || 0) + (selectedGarage?.price || 0) - discount;
    }
    return (dptoPrice || 0) - discount;
  }, [selectedGarage, showGarages, discount, dptoPrice]);

  const paymentsDates = useMemo(() => {
    const [yearInit, monthInit, dayInit] =
      firstPaymentDate?.split('T')[0].split('-') || Array(3).fill('0');

    const [yearFinish, monthFinish, dayFinish] =
      lastPaymentDate?.split('T')[0].split('-') || Array(3).fill('0');
    const dateInit = new Date(+yearInit, +monthInit - 1, +dayInit);
    const dateFinish = new Date(+yearFinish, +monthFinish - 1, +dayFinish);
    const diffUTC = dateFinish.valueOf() - dateInit.valueOf();
    const eachMonth = diffUTC / (paymentsQTY + 1);
    return Array(paymentsQTY)
      .fill('')
      .map((it, index) => {
        const tmpDate = new Date(dateInit.valueOf() + eachMonth * (index + 1));
        return getFormatedMonth(tmpDate);
      });
  }, [firstPaymentDate, lastPaymentDate, paymentsQTY]);

  const paymentPerMonth = (
    (totalAmount * 0.9 - initialCash) /
    Number(paymentsQTY + 1)
  ).toFixed(2);

  const initialPercentage = (initialCash * 100) / totalAmount;
  let sorteo;
  if (selectedBuilding) {
    if (selectedBuilding.name.toLowerCase().includes('apolo')) {
      sorteo = AngelesSorteoApolo;
    } else if (selectedBuilding.name.toLowerCase().includes('bolognesi')) {
      sorteo = AngelesSorteoBolo2;
    }
  }

  return (
    <div className="container">
      {loadingQuoteSummary ? (
        <LoadingComponent
          message="Conectndose a los servidores de Angeles"
          title="Cargando cotizacin"
        />
      ) : (
        <>
          <div className="row mb-3">
            <div className="col">
              <button
                type="button"
                className="btn btn-outline-primary me-5"
                onClick={() => navigate('/quote/list')}
              >
                <i className="fa-solid fa-circle-arrow-left" /> Atrs
              </button>

              <ReactToPrint
                pageStyle="padding: 50px"
                // eslint-disable-next-line react/no-unstable-nested-components
                trigger={() => (
                  <button className="btn btn-primary me-2" type="button">
                    <i className="fa-solid fa-print" />
                    <br />
                    Imprimir
                  </button>
                )}
                content={() => quoteRef.current}
              />
            </div>
          </div>
          <div className="row p-6" ref={quoteRef}>
            <div className="col">
              <div className="row">
                <div
                  className={`${
                    mobile ? 'col-4' : 'col-sm-4'
                  } d-flex justify-content-center align-items-center`}
                >
                  <img
                    src={logoAngeles}
                    style={{ maxWidth: '150px', maxHeight: '150px' }}
                    alt="logo"
                  />
                </div>
                <div
                  className={`${
                    mobile ? 'col-4' : 'col-sm-4'
                  } d-flex justify-content-center align-items-center`}
                >
                  <h1 className="display-4 text-primary">Cotizacin</h1>
                </div>
                <div
                  className={`${
                    mobile ? 'col-4' : 'col-sm-4'
                  } d-flex justify-content-center align-items-center`}
                >
                  <img
                    src={selectedBuilding?.logoUrl}
                    style={{ maxWidth: '150px', maxHeight: '150px' }}
                    alt="logo"
                  />
                </div>
              </div>

              <div className="row mt-3">
                <div className="col-12 ">
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr>
                        <th colSpan={4} className="text-center">
                          DATOS DEL CLIENTE
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>
                          <strong>{`Sr(a/ita/es): `}</strong>
                        </td>
                        <td>{customer?.fullname}</td>
                        <td>
                          <strong>Fecha:</strong>
                        </td>
                        <td>{getFormatedMonth(new Date())}</td>
                      </tr>
                      <tr>
                        <td>
                          <strong>Telfono:</strong>
                        </td>
                        <td>{customer?.phone}</td>
                        <td>
                          <strong>Email:</strong>
                        </td>
                        <td>{customer?.email}</td>
                      </tr>
                      <tr>
                        <td colSpan={4} className="text-center">
                          <strong>Validez:</strong> <span>7 das</span>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>

              <div className="row">
                <div className="col ">
                  <p>
                    Gracias por contactarse con nosotros, le otorgamos la
                    cotizacin solicitada.
                  </p>
                </div>
              </div>

              <div className="row">
                <div className={mobile ? 'col-6' : 'col-sm-6'}>
                  <div className="table-responsive">
                    <table className="table table-sm table-bordered">
                      <thead>
                        <tr>
                          <th className="text-center" colSpan={2}>
                            DATOS DE LA PROPIEDAD
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>
                            <strong>Proyecto:</strong>
                          </td>
                          <td align="center">{selectedBuilding?.name}</td>
                        </tr>
                        <tr>
                          <td>
                            <strong>Piso:</strong>
                          </td>
                          <td align="center">
                            {selectedDpto?.floor}
                            {` (${nameNumber[selectedDpto?.floor || 0]})`}
                          </td>
                        </tr>
                        <tr>
                          <td>
                            <strong>Metraje:</strong>
                          </td>
                          <td align="center">
                            {selectedDpto?.typology?.area} m<sup>2</sup>
                          </td>
                        </tr>
                        <tr>
                          <td>
                            <strong>Tipologa</strong>
                          </td>
                          <td align="center">{selectedDpto?.typology?.name}</td>
                        </tr>
                        {showGarages && selectedGarage && (
                          <tr>
                            <td>
                              <strong>Cochera:</strong>
                            </td>
                            <td align="center">{selectedGarage.name}</td>
                          </tr>
                        )}
                        <tr>
                          <td>
                            <strong>Modalidad de compra:</strong>
                          </td>
                          <td align="center">
                            {isCash ? 'Al contado' : 'Crdito hipotecario'}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
                <div className={mobile ? 'col-3' : 'col-sm-3'}>
                  <div className="row">
                    <div className="col">
                      <div className="table-responsive">
                        <table className="table table-sm table-bordered">
                          <thead>
                            <tr>
                              <th className="text-center">Precio Dpto.:</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td className="text-center">
                                <NumberFormat
                                  value={dptoPrice}
                                  displayType="text"
                                  thousandSeparator
                                  prefix="S/ "
                                />
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                  {selectedGarage && showGarages ? (
                    <div className="row">
                      <div className="col">
                        <div className="table-responsive">
                          <table className="table table-sm table-bordered">
                            <thead>
                              <tr>
                                <th className="text-center">Precio Cochera:</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td className="text-center">
                                  <NumberFormat
                                    value={selectedGarage?.price}
                                    displayType="text"
                                    thousandSeparator
                                    prefix="S/ "
                                  />
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </div>
                  ) : null}
                  <div className="row">
                    <div className="col">
                      <div className="table-responsive">
                        <table className="table table-sm table-bordered">
                          <thead>
                            <tr>
                              <th className="text-center">Total:</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td className="text-center">
                                <NumberFormat
                                  value={
                                    (dptoPrice || 0) +
                                    (selectedGarage?.price || 0)
                                  }
                                  displayType="text"
                                  thousandSeparator
                                  prefix="S/ "
                                />
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
                <div className={mobile ? 'col-3' : 'col-sm-3'}>
                  <div className="table-responsive">
                    <table className="table table-sm table-bordered">
                      <thead>
                        <tr>
                          <th className="text-center">CARACTERSTICAS</th>
                        </tr>
                      </thead>
                      <tbody>
                        {selectedBuilding?.services?.map((s) => (
                          <tr key={s.id}>
                            <td className="text-center">* {s.name}</td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col">
                  <div className="table-responsive">
                    <table className="table table-sm table-bordered">
                      <thead>
                        <tr>
                          <th
                            colSpan={showGarages && selectedGarage ? 1 : 2}
                            className="text-center"
                          >
                            TIPOLOGA
                          </th>
                          {showGarages && selectedGarage ? (
                            <th className="text-center">COCHERA</th>
                          ) : (
                            <th className="text-center">PROMOCIN</th>
                          )}
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td
                            align="center"
                            colSpan={showGarages && selectedGarage ? 1 : 2}
                          >
                            <img
                              src={selectedDpto?.typology?.url}
                              style={{ maxHeight: '395px' }}
                              alt="tipologa"
                            />
                          </td>
                          {showGarages && selectedGarage ? (
                            <td align="center">
                              <img
                                src={selectedGarage?.url}
                                style={{ maxHeight: '395px' }}
                                alt="imagen de garage"
                              />
                            </td>
                          ) : (
                            <td align="center">
                              {sorteo ? (
                                <img
                                  src={sorteo}
                                  style={{
                                    maxHeight: '395px',
                                    maxWidth: '100%',
                                  }}
                                  alt="imagen de promocion"
                                />
                              ) : (
                                <img
                                  src={logoAngeles}
                                  style={{
                                    maxHeight: '395px',
                                    maxWidth: '100%',
                                  }}
                                  alt="imagen de promocion"
                                />
                              )}
                            </td>
                          )}
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col ">
                  <table className="table table-bordered table-sm">
                    <thead>
                      <tr>
                        <th colSpan={2} className="text-center">
                          PRECIO
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td className="text-end fw-bold">Precio Dpto:</td>
                        <td align="center">
                          <NumberFormat
                            value={dptoPrice}
                            displayType="text"
                            thousandSeparator
                            prefix="S/ "
                            className="text-center border-0 bg-transparent"
                          />
                        </td>
                      </tr>
                      {showGarages && (
                        <tr>
                          <td className="text-end fw-bold">Precio Cochera:</td>
                          <td align="center">
                            <NumberFormat
                              value={selectedGarage?.price || 0}
                              displayType="text"
                              thousandSeparator
                              prefix="S/ "
                              className="text-center border-0 bg-transparent"
                            />
                          </td>
                        </tr>
                      )}

                      <tr>
                        <td className="text-end fw-bold">
                          Descuento promocional:
                        </td>
                        <td align="center">
                          <NumberFormat
                            disabled
                            value={discount}
                            displayType="input"
                            thousandSeparator
                            prefix="S/ - ("
                            suffix=")"
                            className="bg-transparent border-0 text-danger text-center"
                          />
                        </td>
                      </tr>
                      <tr>
                        <td className="text-end fw-bold">
                          Cuota Inicial {initialPercentage.toFixed(2)}%:{' '}
                        </td>
                        <td align="center">
                          <NumberFormat
                            readOnly
                            value={initialCash}
                            displayType="text"
                            thousandSeparator
                            prefix="S/ "
                            className="bg-transparent fw-bold p-0 text-primary"
                          />
                        </td>
                      </tr>
                      <tr>
                        <td className="text-end fw-bold">Total:</td>
                        <td align="center">
                          <NumberFormat
                            value={totalAmount}
                            displayType="text"
                            thousandSeparator
                            prefix="S/ "
                            className="text-center fw-bold bg-transparent border-0 text-success fs-5"
                          />
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
              <div className="row">
                <div className="col ">
                  <div className="row d-flex align-items-start ">
                    <div className="table-responsive col">
                      <table className="table table-bordered table-sm">
                        <thead>
                          <tr>
                            <th className="text-center" colSpan={4}>
                              FORMA DE PAGO
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr className="text-center">
                            <th>.</th>
                            <th>Porcentaje</th>
                            <th>Monto</th>
                            <th>Fechas de pago</th>
                          </tr>
                          <tr>
                            <td className="text-end">
                              <strong>Cuota Inicial:</strong>
                            </td>
                            <td align="center">
                              <NumberFormat
                                value={(
                                  (initialCash * 100) /
                                  totalAmount
                                ).toFixed(2)}
                                displayType="text"
                                suffix=" %"
                              />
                            </td>
                            <td>
                              <NumberFormat
                                disabled
                                value={initialCash}
                                displayType="input"
                                thousandSeparator
                                prefix="S/ "
                                className="bg-transparent border-0 fs-4 p-0 form-control text-center text-primary"
                              />
                            </td>
                            <td align="center">
                              <span>
                                {!isCash ? (
                                  <span>Por definir</span>
                                ) : (
                                  <span>
                                    {getFormatedDateByStringDate(
                                      firstPaymentDate || ''
                                    )}
                                  </span>
                                )}
                              </span>
                            </td>
                          </tr>
                          {paymentsQTY > 0 &&
                            Array(paymentsQTY)
                              .fill('')
                              .map((_, idx) => (
                                <tr key={idx}>
                                  <td className="text-end">
                                    <strong>Cuota {idx + 1}:</strong>
                                  </td>
                                  <td align="center">
                                    <span>
                                      {`${(
                                        (+paymentPerMonth * 100) /
                                        totalAmount
                                      ).toFixed(2)} %`}
                                    </span>
                                  </td>
                                  <td align="center">
                                    <NumberFormat
                                      value={paymentPerMonth}
                                      displayType="text"
                                      thousandSeparator
                                      prefix="S/ "
                                    />
                                  </td>

                                  <td align="center">
                                    <span>{paymentsDates.at(idx)}</span>
                                  </td>
                                </tr>
                              ))}
                          {isCash && (
                            <tr>
                              <td className="text-end">
                                <strong>Entrega de bien fsico:</strong>
                              </td>
                              <td align="center">
                                <span>
                                  {`${(
                                    (+paymentPerMonth * 100) /
                                    totalAmount
                                  ).toFixed(2)} %`}
                                </span>
                              </td>
                              <td align="center">
                                <NumberFormat
                                  value={paymentPerMonth}
                                  displayType="text"
                                  thousandSeparator
                                  prefix="S/ "
                                />
                              </td>

                              <td align="center">
                                <span>
                                  {getFormatedDateByStringDate(
                                    lastPaymentDate || ''
                                  )}
                                </span>
                              </td>
                            </tr>
                          )}
                          {/* ULTIMA CUOTA 10% */}
                          <tr>
                            <td className="text-end">
                              <strong>
                                {isCash
                                  ? 'Formalizacin de venta:'
                                  : 'Monto Financiado:'}
                              </strong>
                            </td>
                            <td align="center">
                              {isCash ? (
                                <span>10 %</span>
                              ) : (
                                <NumberFormat
                                  value={(
                                    ((totalAmount - initialCash) * 100) /
                                    totalAmount
                                  ).toFixed(2)}
                                  displayType="text"
                                  thousandSeparator
                                  suffix=" %"
                                />
                              )}
                            </td>
                            <td align="center">
                              <NumberFormat
                                value={
                                  isCash
                                    ? totalAmount * 0.1
                                    : totalAmount - initialCash
                                }
                                displayType="text"
                                thousandSeparator
                                prefix="S/ "
                              />
                            </td>
                            <td rowSpan={2} />
                          </tr>
                          {/* FILA TOTAL */}
                          <tr>
                            <td className="text-end">
                              <strong>Total:</strong>
                            </td>
                            <td align="center">
                              <span>100 %</span>
                            </td>
                            <td align="center">
                              <NumberFormat
                                value={totalAmount}
                                displayType="text"
                                thousandSeparator
                                prefix="S/ "
                              />
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row justify-content-center">
                <div className={mobile ? 'col-6' : 'col-sm-6'}>
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <th colSpan={2} className="text-center">
                          DATOS DEL VENDEDOR
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>
                          <strong>Nombre:</strong>
                        </td>
                        <td>{user?.fullname}</td>
                      </tr>
                      <tr>
                        <td>
                          <strong>Telfono:</strong>
                        </td>
                        <td>{user?.phone}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default PageQuoteSummary;
