import React, { useContext, useMemo, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { BuildingsResponse } from '../../services/buildingservice';
import GenericModal from '../../shared/components/GenericModal';
import { FormContext } from '../context/QuoteRegisterContext';
import { DPTO_STATUS } from '../../constants';

interface FormStep1DptoInfoProps {
  buildings: BuildingsResponse[];
  SW: any;
}

const FormStep1DptoInfo = (props: FormStep1DptoInfoProps) => {
  const { buildings, SW } = props;

  const formContext = useContext(FormContext);
  const {
    isCash,
    setIsCash,
    selectedBuilding,
    selectedDpto,
    selectedGarage,
    setSelectedBuilding,
    setSelectedDpto,
    setSelectedGarage,
    showGarages,
    setShowGarages,
  } = formContext;

  const [showImageModal, setShowImageModal] = useState(false);
  const [showModalGarage, setShowModalGarage] = useState(false);

  const formik = useFormik({
    initialValues: {
      building: 0,
      dpto: 0,
      garage: 0,
      withGarage: showGarages,
    },
    validationSchema: Yup.object({
      building: Yup.string().notOneOf(['0'], 'Debe seleccionar un proyecto'),
      dpto: Yup.string().notOneOf(['0'], 'Debe seleccionar un departamento'),
      garage: Yup.string().when(['withGarage'], (withGarage) => {
        if (withGarage) {
          return Yup.string().notOneOf(
            ['0'],
            'Si desea cotizar cochera, debe elegir una'
          );
        }
        return Yup.string();
      }),
    }),
    onSubmit: () => {
      SW.nextStep();
    },
  });
  const {
    values: { withGarage },
    setValues,
  } = formik;

  const handleChangeBuilding = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value: id } = e.target;
    const buildingFound = buildings.find((b) => b.id === +id);
    setSelectedBuilding(buildingFound);
    setSelectedDpto(undefined);
    setSelectedGarage(undefined);
    setShowGarages(false);
  };

  const handleChangeDpto = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value: id } = e.target;
    const dptoFound = selectedBuilding.dptos.find((d) => d.id === +id);
    setValues((values) => ({ ...values, withGarage: false }));
    setShowGarages(false);
    setSelectedGarage(undefined);
    setSelectedDpto(dptoFound);
  };

  const handleChangeGarage = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value: id } = e.target;
    const garageFound = selectedBuilding.garages.find((g) => g.id === +id);
    setSelectedGarage(garageFound);
  };

  const availableDptos = useMemo(
    () =>
      selectedBuilding?.dptos.map((d) => ({
        ...d,
        label: `${d.status !== 0 ? DPTO_STATUS[d.status] : ''} ${d.number} / ${
          d.typology?.name
        } / (${d.typology?.area} mts)`,
      })) || [],
    [selectedBuilding]
  );

  const availableGarages = useMemo(
    () =>
      selectedBuilding?.garages.map((g) => ({
        ...g,
        label: `${g.status !== 0 ? DPTO_STATUS[g.status] : ''} ${
          g.name
        } / S/ (${g.price.toFixed(2)})`,
        value: g.id,
      })) || [],
    [selectedBuilding]
  );

  return (
    <>
      <GenericModal
        show={showImageModal}
        setShow={setShowImageModal}
        title={selectedDpto?.typology?.name || ''}
      >
        <div className="row">
          <div className="col text-center">
            <img
              src={
                selectedDpto?.typology?.url ||
                'https://via.placeholder.com/250?text=no%20disponible'
              }
              alt=""
              style={{ maxWidth: '100%', maxHeight: 'calc(100vh - 200px)' }}
            />
          </div>
        </div>
      </GenericModal>

      <GenericModal
        show={showModalGarage}
        setShow={setShowModalGarage}
        title={selectedGarage?.name || ''}
      >
        <div className="row">
          <div className="col text-center">
            <img
              src={
                selectedGarage?.url ||
                'https://via.placeholder.com/250?text=no%20disponible'
              }
              alt=""
              style={{ maxWidth: '100%', maxHeight: 'calc(100vh - 200px)' }}
            />
          </div>
        </div>
      </GenericModal>

      <div className="row mb-3">
        <div className="col-md-6">
          <form onSubmit={formik.handleSubmit} noValidate autoComplete="off">
            <div className="mb-3">
              <label htmlFor="building" className="form-label">
                Seleccione Proyecto:
              </label>
              <select
                name="building"
                id="building"
                className="form-control"
                placeholder="Proyectos disponibles"
                onChange={(e) => {
                  handleChangeBuilding(e);
                  formik.handleChange(e);
                }}
                value={selectedBuilding?.id || 0}
              >
                <option value={0}>Seleccione</option>
                {buildings.map((b) => (
                  <option value={b.id} key={b.id}>
                    {b.name}
                  </option>
                ))}
              </select>
              {formik.errors.building ? (
                <small className="text-danger">{formik.errors.building}</small>
              ) : null}
            </div>
            <div className="mb-3">
              <label htmlFor="dpto" className="form-label">
                Seleccione Dpto:
              </label>
              <select
                name="dpto"
                id="dpto"
                className="form-control"
                placeholder="Departamentos disponibles"
                onChange={(e) => {
                  handleChangeDpto(e);
                  formik.handleChange(e);
                }}
                value={selectedDpto?.id || 0}
              >
                <option value={0}>Seleccione</option>
                {availableDptos.map((d) => (
                  <option value={d.id} key={d.id} disabled={d.status !== 0}>
                    {d.label}
                  </option>
                ))}
              </select>
              {formik.errors.dpto ? (
                <small className="text-danger">{formik.errors.dpto}</small>
              ) : null}
            </div>
            <div className="mb-3">
              {withGarage && (
                <div>
                  <label htmlFor="garage" className="form-label">
                    Seleccione Cochera:
                  </label>
                  <select
                    name="garage"
                    id="garage"
                    className="form-control"
                    placeholder="Garajes disponibles"
                    onChange={(e) => {
                      handleChangeGarage(e);
                      formik.handleChange(e);
                    }}
                    value={selectedGarage?.id || 0}
                  >
                    <option value={0}>Seleccione</option>
                    {availableGarages.map((g) => (
                      <option
                        value={g.value}
                        key={g.value}
                        disabled={g.status !== 0}
                      >
                        {g.label}
                      </option>
                    ))}
                  </select>
                  {formik.errors.garage ? (
                    <small className="text-danger">
                      {formik.errors.garage}
                    </small>
                  ) : null}
                </div>
              )}
            </div>
            <div className="row">
              <div className="col-6">
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="is-cash"
                    id="radio-cash"
                    checked={isCash}
                    onChange={(e) => setIsCash(e.target.checked)}
                  />
                  <label className="form-check-label" htmlFor="radio-cash">
                    Pago al contado
                  </label>
                </div>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="is-cash"
                    id="radio-non-cash"
                    checked={!isCash}
                    onChange={(e) => setIsCash(!e.target.checked)}
                  />
                  <label className="form-check-label" htmlFor="radio-non-cash">
                    Crdito hipotecario
                  </label>
                </div>
              </div>
              <div className="col-6">
                {selectedDpto && selectedDpto.apply_for_garage && (
                  <div className="form-check form-switch">
                    <label className="form-check-label" htmlFor="withGarage">
                      Incluir Cochera
                    </label>
                    <input
                      name="withGarage"
                      className="form-check-input"
                      type="checkbox"
                      role="switch"
                      id="withGarage"
                      checked={withGarage}
                      onChange={(e) => {
                        setShowGarages(e.target.checked);
                        setSelectedGarage(undefined);
                        formik.handleChange(e);
                      }}
                    />
                  </div>
                )}
              </div>
            </div>
          </form>
        </div>
        <div className="col-md-3 text-center">
          <table className="table">
            <thead>
              <tr>
                <th>Tipologa</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <div
                    tabIndex={-7}
                    onClick={() => setShowImageModal(true)}
                    role="button"
                  >
                    <img
                      style={{ maxWidth: '150px', maxHeight: '150px' }}
                      className="img-thumbnail cursor-pointer"
                      src={
                        selectedDpto?.typology?.url ||
                        'https://via.placeholder.com/250?text=no%20disponible'
                      }
                      alt="typology thumbnail"
                    />
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="col-md-3 text-center">
          {selectedGarage && withGarage ? (
            <table className="table">
              <thead>
                <tr>
                  <th>Garage</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <div
                      tabIndex={-6}
                      role="button"
                      onClick={() => setShowModalGarage(true)}
                    >
                      <img
                        style={{ maxWidth: '150px', maxHeight: '150px' }}
                        className="img-thumbnail cursor-pointer"
                        src={
                          selectedGarage?.url ||
                          'https://via.placeholder.com/250?text=no%20disponible'
                        }
                        alt="typology thumbnail"
                      />
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          ) : null}
        </div>
      </div>

      <div className="row">
        <div className="col text-end">
          <button
            className="btn btn-primary"
            onClick={() => formik.submitForm()}
            type="submit"
          >
            Siguiente <i className="fa-solid fa-angle-right" />
          </button>
        </div>
      </div>
    </>
  );
};

export default FormStep1DptoInfo;
