import React, { useContext, useState } from 'react';

import StepWizard from 'react-step-wizard';
import { BuildingsResponse } from '../../services/buildingservice';
import FormStep1DptoInfo from './FormStep1DptoInfo';
import FormStep2ClientInfo from './FormStep2ClientInfo';
import FormStep3PDF from './FormStep3PDF';
import FormStep4Rating from './FormStep4Rating';
import Stepper from './Stepper';
import { FormContext } from '../context/QuoteRegisterContext';
import FormPrices from './FormPrices';
import FormPaymentList from './FormPaymentList';

const FormQuoteRegister = ({
  buildings,
}: {
  buildings: BuildingsResponse[];
}) => {
  const [SW, setSW] = useState<any>();
  const formContext = useContext(FormContext);
  const { paymentsQty, modePDF, isCash } = formContext;

  const setInstance = (SW: any) => setSW(SW);

  return (
    <>
      <div className="card shadow">
        <div className="card-body">
          <StepWizard instance={setInstance} className="mb-3" nav={<Stepper />}>
            <FormStep1DptoInfo SW={SW} buildings={buildings} />
            <FormStep2ClientInfo SW={SW} />
            <FormStep3PDF SW={SW} />
            <FormStep4Rating SW={SW} />
          </StepWizard>
        </div>
      </div>
      {!modePDF && (
        <>
          <hr />
          <div className="card shadow">
            <div className="card-header">
              <h5 className="text-center">PRECIO</h5>
            </div>
            <div className="card-body">
              <FormPrices />
            </div>
          </div>

          {isCash && (
            <>
              <hr />
              <div className="card shadow border-primary">
                <div className="card-header">
                  <h5 className="text-center">
                    PAGO FRACCIONADO AL CONTADO
                    <strong className="text-primary">
                      : {paymentsQty + 2} cuotas
                    </strong>
                  </h5>
                </div>
                <div className="card-body">
                  <FormPaymentList />
                </div>
              </div>
            </>
          )}
        </>
      )}
    </>
  );
};

export default FormQuoteRegister;
