import React from 'react';
import { INTERESTED, NOT_INTERESTED, VERY_INTERESTED } from '../../constants';

interface Props {
  interested: number;
  saveInterest: (n: number) => void;
}

export const InterestButton = ({ interested, saveInterest }: Props) => (
  <>
    <button
      type="button"
      onClick={() => saveInterest(NOT_INTERESTED)}
      className={`btn ${
        interested === NOT_INTERESTED ? ' btn-danger' : ' btn-outline-danger'
      }`}
    >
      {interested === NOT_INTERESTED && (
        <i className="fa-solid fa-check me-1" />
      )}
      Poco interesado <i className="fa-solid fa-face-frown-open" />
    </button>
    <button
      type="button"
      onClick={() => saveInterest(INTERESTED)}
      className={`btn ${
        interested === INTERESTED ? ' btn-warning' : ' btn-outline-warning'
      }`}
    >
      {interested === INTERESTED && <i className="fa-solid fa-check me-1" />}
      Meh <i className="fa-solid fa-face-meh" />
    </button>
    <button
      type="button"
      onClick={() => saveInterest(VERY_INTERESTED)}
      className={`btn ${
        interested === VERY_INTERESTED ? ' btn-success' : ' btn-outline-success'
      }`}
    >
      {interested === VERY_INTERESTED && (
        <i className="fa-solid fa-check me-1" />
      )}
      Muy interesado <i className="fa-solid fa-face-smile" />
    </button>
  </>
);
