import React from 'react';

interface Props {
  totalSteps: number;
  currentStep: number;
  step: number;
}
const stepsInfo = {
  '1': 'Datos del departamento',
  '2': 'Datos del cliente',
  '3': 'Resumen de cotizacin',
  '4': 'Categorizacin',
};

const Step = ({ currentStep, totalSteps, step }: Props) => {
  const centerContainer =
    'col d-flex justify-content-center align-items-center';
  return (
    <>
      <div
        style={{ width: '100px', height: '80px' }}
        className="d-flex flex-column justify-content-center align-items-center"
      >
        <button
          type="button"
          className={`btn rounded-circle btn-${
            currentStep >= step ? 'primary' : 'secondary'
          }`}
          style={{ width: '40px', height: '40px' }}
          disabled={currentStep < step}
        >
          {step}
        </button>
        <p className={`text-center text-${currentStep < step && 'secondary'}`}>
          {stepsInfo[step]}
        </p>
      </div>
      {totalSteps === step ? null : (
        <div className={centerContainer}>
          <div
            className={`bg-${currentStep > step ? 'primary' : 'light'}`}
            style={{ height: '5px', flex: 1 }}
          />
        </div>
      )}
    </>
  );
};

export default Step;
