import React, { FormEvent, useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import { getUsers, postLogin } from '../../services/publicservice';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { UserObj } from '../../types/types';
import LoadingButton from '../../shared/components/LoadingButton';
import { setToken } from '../../utils/token';
import { routesCollection } from '../../routes/contract';
import logo from '../../assets/logo.png';

const PageLogin = () => {
  const [loadingUsers, setLoadingUsers] = useState(true);
  const [users, setUsers] = useState<UserObj[]>([]);
  const [selectedUser, setSelectedUser] = useState<number>(0);
  const [password, setPassword] = useState('');
  const [loadingLogin, setLoadingLogin] = useState(false);
  const navigate = useNavigate();
  const getUserCall = async () => {
    setLoadingUsers(true);
    const response = await getUsers();
    if (response.ok) {
      setUsers(response.content);
    }
    setLoadingUsers(false);
  };

  useEffect(() => {
    getUserCall();
  }, []);

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (selectedUser === 0 || password.trim().length === 0) {
      toast('Ups! faltan completar todos los campos', { type: 'error' });
    } else {
      try {
        setLoadingLogin(true);
        const response = await postLogin({ id: +selectedUser!, password });
        if (response.ok) {
          setToken(response.content);
          navigate(routesCollection.QUOTE_REGISTER);
          toast('Inicio de sesin correcto', { type: 'success' });
        } else {
          toast('error', { type: 'error' });
        }
        setLoadingLogin(false);
      } catch (error) {
        toast('error', { type: 'error' });
        setLoadingLogin(false);
      }
    }
  };

  return (
    <>
      <div
        className="headerquote container-fluid"
        style={{ marginBottom: '100px' }}
      >
        <div className="image_container">
          <img src={logo} alt="" />
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-md-6">
          <div className="card shadow">
            <div className="card-body">
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  {loadingUsers ? (
                    <LoadingComponent
                      title="Cargando usuarios"
                      message="Cargando la lista de usuarios de la base de datos en Angeles"
                    />
                  ) : (
                    <>
                      <label htmlFor="username" className="form-label">
                        Seleccione usuario:
                      </label>
                      <select
                        className="form-control"
                        id="username"
                        onChange={(e) => setSelectedUser(+e.target.value)}
                        value={selectedUser}
                      >
                        <option value={0}>Seleccione</option>
                        {users.map((user, idx) => (
                          <option value={user.id} key={idx}>
                            {user.fullname}
                          </option>
                        ))}
                      </select>
                    </>
                  )}
                </div>
                <div className="mb-3">
                  <label htmlFor="userpassword" className="form-label">
                    Contrasea:
                  </label>
                  <input
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    type="password"
                    className="form-control"
                    id="userpassword"
                  />
                </div>
                <LoadingButton
                  type="submit"
                  className="btn btn-primary"
                  disabled={
                    loadingLogin ||
                    +selectedUser! === 0 ||
                    password.trim().length === 0
                  }
                  loading={loadingLogin}
                >
                  Iniciar Sesin
                </LoadingButton>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default PageLogin;
