import React, { useState } from 'react';
import { BuildingsResponse } from '../../services/buildingservice';
import {
  CustomerObj,
  DptoObj,
  GarageObj,
  TypologyObj,
} from '../../types/types';
import { FormContext } from './QuoteRegisterContext';

const defaults = {
  modePDF: false,
  initialCashDate: '2022-08-15',
  finalCashDate: '2023-12-29',
  initialCash: 0,
  paymentsQty: 0,
  isCash: false,
  customer: undefined,
  selectedBuilding: undefined,
  selectedGarage: undefined,
  discount: 0,
  showGarages: false,
  selectedDpto: undefined,
  savedQuoteId: 0,
};
interface Props {
  children: React.ReactNode;
}
const FormState = ({ children }: Props) => {
  const [selectedBuilding, setSelectedBuilding] = useState<
    BuildingsResponse | undefined
  >(defaults.selectedBuilding);
  const [selectedDpto, setSelectedDpto] = useState<
    (DptoObj & { typology: TypologyObj }) | undefined
  >(defaults.selectedDpto);
  const [modePDF, setModePDF] = useState(defaults.modePDF);
  const [initialCashDate, setInitialCashDate] = useState<string>(
    defaults.initialCashDate
  );
  const [finalCashDate, setFinalCashDate] = useState<string>(
    defaults.finalCashDate
  );
  const [savedQuoteId, setSavedQuoteId] = useState<number>(
    defaults.savedQuoteId
  );
  const [initialCash, setInitialCash] = useState(defaults.initialCash);
  const [paymentsQty, setPaymentsQty] = useState(defaults.paymentsQty);
  const [isCash, setIsCash] = useState(defaults.isCash);
  const [customer, setCustomer] = useState<CustomerObj | undefined>(
    defaults.customer
  );
  const [selectedGarage, setSelectedGarage] = useState<GarageObj | undefined>(
    defaults.selectedGarage
  );
  const [discount, setDiscount] = useState(defaults.discount);
  const [showGarages, setShowGarages] = useState(defaults.showGarages);

  const resetQuote = () => {
    setSelectedBuilding(defaults.selectedBuilding);
    setSelectedDpto(defaults.selectedDpto);
    setModePDF(defaults.modePDF);
    setInitialCashDate(defaults.initialCashDate);
    setFinalCashDate(defaults.finalCashDate);
    setInitialCash(defaults.initialCash);
    setPaymentsQty(defaults.paymentsQty);
    setIsCash(defaults.isCash);
    setCustomer(defaults.customer);
    setSelectedGarage(defaults.selectedGarage);
    setDiscount(defaults.discount);
    setShowGarages(defaults.showGarages);
    setSavedQuoteId(defaults.savedQuoteId);
  };

  return (
    <FormContext.Provider
      value={{
        savedQuoteId,
        paymentsQty,
        initialCash,
        discount,
        modePDF,
        selectedDpto,
        selectedGarage,
        selectedBuilding,
        customer,
        isCash,
        initialCashDate,

        finalCashDate,

        showGarages,
        resetQuote,

        setShowGarages,
        setDiscount,
        setSelectedGarage,
        setSelectedDpto,
        setSelectedBuilding,
        setCustomer,
        setModePDF,
        setInitialCashDate,
        setFinalCashDate,
        setIsCash,
        setInitialCash,
        setPaymentsQty,
        setSavedQuoteId,
      }}
    >
      {children}
    </FormContext.Provider>
  );
};

export default FormState;
