import React, { useContext, useState } from 'react';
import Swal from 'sweetalert2';
import { toast } from 'react-toastify';
import { FormContext } from '../context/QuoteRegisterContext';
import LoadingButton from '../../shared/components/LoadingButton';
import { putQuoteInterestByQuoteId } from '../../services/quoteservice';
import { InterestButton } from '../../shared/components/InterestButton';
import LoadingComponent from '../../shared/components/LoadingComponent';

interface FormStep4PDFProps {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  SW?: any;
}
const FormStep4Rating = (props: FormStep4PDFProps) => {
  const { SW } = props;
  const formContext = useContext(FormContext);
  const [interested, setInterested] = useState(2);
  const [loadingInterest, setLoadingInterest] = useState(false);
  const { savedQuoteId: quoteId, resetQuote } = formContext;

  const handleSaveInterest = async () => {
    const result = await Swal.fire({
      title: 'Cambiar nivel de inters del cliente?',
      text: 'Se guardarn los cambios en la base de datos',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'S guardar',
      cancelButtonText: 'Cancelar',
    });

    if (result.isConfirmed) {
      try {
        setLoadingInterest(true);
        const response = await putQuoteInterestByQuoteId(quoteId, interested);
        if (response.ok) {
          setLoadingInterest(false);
          toast('Nivel de inters guardado exitosamente', { type: 'success' });
        }
      } catch (error) {
        setLoadingInterest(false);
      }
    }
  };
  return (
    <div className="container">
      {loadingInterest ? (
        <LoadingComponent
          title="Guardando inters"
          message="Contectndonos a la base de datos de Angeles"
        />
      ) : (
        <>
          <div className="row">
            <div className="col d-flex justify-content-center align-items-center gap-4">
              <InterestButton
                interested={interested}
                saveInterest={setInterested}
              />
            </div>
          </div>
          <div className="row mt-3">
            <div className="col d-flex justify-content-center gap-4">
              <LoadingButton
                disabled={false}
                loading={false}
                className="btn btn-primary"
                onClick={handleSaveInterest}
              >
                <i className="fa-solid fa-floppy-disk me-2" /> Guardar
              </LoadingButton>
              <button
                type="button"
                className="btn btn-outline-danger"
                onClick={() => {
                  resetQuote();
                  SW.goToStep(1);
                }}
              >
                <i className="fa-solid fa-flag me-1" />
                Finalizar Cotizacin
              </button>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default FormStep4Rating;
