import { CommonResponse } from './buildingservice';
import { client } from '../httpClient';

export const getPersonByDni = async (dni: string) => {
  try {
    const url = `/reniec/get-person-by-dni/${dni}`;
    const response = await client(url);
    return response.data as CommonResponse<{
      isNew: boolean;
      dni: string;
      fullname: string;
      email?: string;
      phone?: string;
    }>;
  } catch (error) {}
};
