import { useFormik } from 'formik';
import React, { useContext, useEffect, useState } from 'react';
import * as Yup from 'yup';
import { toast } from 'react-toastify';
import { getPersonByDni } from '../../services/reniecservice';
import LoadingButton from '../../shared/components/LoadingButton';
import { FormContext } from '../context/QuoteRegisterContext';

const availableMails = [
  { label: '@gmail.com', value: '@gmail.com' },
  { label: '@hotmail.com', value: '@hotmail.com' },
  { label: 'other', value: 'other' },
];
const FormStep2ClientInfo = (props: { SW: any }) => {
  const { SW } = props;
  const [mailType, setMailType] = useState(availableMails[0].value);
  const [loadingDNI, setLoadingDNI] = useState(false);
  const formContext = useContext(FormContext);
  const { setModePDF, setCustomer, customer } = formContext!;

  const formik = useFormik({
    initialValues: {
      dni: customer?.dni || '',
      email: customer?.email || '',
      phone: customer?.phone || '',
      name: customer?.fullname || '',
    },
    validationSchema: Yup.object({
      phone: Yup.string().required('El telfono es obligatorio'),
      dni: Yup.string().required('El dni es obligatorio'),
      name: Yup.string().required('El nombre es obligatorio'),
    }),
    onSubmit: (formData) => {
      const { dni, email, name, phone } = formData;
      const completeEmail =
        mailType !== 'other' ? email.trim().concat(mailType) : email.trim();
      setCustomer({
        dni,
        email: completeEmail,
        fullname: name,
        phone,
      });
      setModePDF(true);
      SW.nextStep();
    },
  });

  useEffect(() => {
    if (!customer) {
      formik.resetForm();
    }
  }, [customer]);

  const getPersonByDniCall = () => {
    formik.resetForm({
      values: { email: '', phone: '', name: '', dni: formik.values.dni },
    });
    setLoadingDNI(true);
    getPersonByDni(formik.values.dni)
      .then((data) => {
        if (data.ok) {
          if (!data.content.isNew) {
            formik.setFieldValue('email', data.content.email);
            formik.setFieldValue('phone', data.content.phone);
          }
          formik.setFieldValue('name', data.content.fullname);
          toast('Cliente encontrado!', { type: 'success' });
        } else {
          toast('Cliente no encontrado =( ', { type: 'error' });
        }
        setLoadingDNI(false);
      })
      .catch(() => {
        toast('Cliente no encontrado =( ', { type: 'error' });
        setLoadingDNI(false);
      });
  };
  const handleChangeSelectMail = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setMailType(e.target.value);
  };
  useEffect(() => setMailType(availableMails[0].value), []);
  return (
    <form autoComplete="off" noValidate onSubmit={formik.handleSubmit}>
      <div className="row g-0 mb-3">
        <div className="col-md-8 col-sm-6">
          <div className="form-floating">
            <input
              name="dni"
              id="dni"
              maxLength={8}
              className="form-control form-control-lg"
              type="text"
              value={formik.values.dni}
              onChange={formik.handleChange}
              placeholder="Ejm: 12345678"
              onKeyUp={(e) => {
                if (e.charCode === 13 || e.keyCode === 13) {
                  getPersonByDniCall();
                }
              }}
            />
            {formik.errors.dni && formik.touched.dni ? (
              <small className="text-danger">{formik.errors.dni}</small>
            ) : null}
            <label htmlFor="dni" className="form-label">
              Ingrese el dni:
            </label>
          </div>
        </div>
        <div className="col-md-4 col-sm-6">
          <LoadingButton
            loading={loadingDNI}
            disabled={loadingDNI}
            className="btn btn-secondary btn-lg w-100 h-100"
            type="button"
            onClick={getPersonByDniCall}
          >
            <i className="fa-solid fa-magnifying-glass me-2" />
            Buscar
          </LoadingButton>
        </div>
      </div>

      <div className="form-floating mb-3">
        <input
          name="name"
          id="name"
          className="form-control form-control-lg"
          type="text"
          value={formik.values.name}
          onChange={formik.handleChange}
          placeholder="Ejm: jorge"
        />
        {formik.errors.name && formik.touched.name ? (
          <small className="text-danger">{formik.errors.name}</small>
        ) : null}
        <label htmlFor="name" className="form-label">
          Nombre completo:
        </label>
      </div>

      <div className="row  mb-3 g-0">
        <div className="col-7 ">
          <div className="form-floating">
            <input
              name="email"
              id="email"
              className="form-control form-control-lg"
              type="email"
              value={formik.values.email}
              onChange={formik.handleChange}
              placeholder="Ejm: jorge"
            />
            <label htmlFor="email" className="form-label">
              Ingrese el email:
            </label>
          </div>
        </div>
        <div className="col-5">
          <select
            id="select-email"
            className="form-control form-control-lg"
            value={mailType}
            onChange={handleChangeSelectMail}
          >
            {availableMails.map((mail, idx) => (
              <option value={mail.value} key={idx}>
                {mail.label}
              </option>
            ))}
          </select>
        </div>
      </div>

      <div className="row mb-3">
        <div className="col-md-6">
          <div className="form-floating">
            <input
              name="phone"
              id="phone"
              className="form-control form-control-lg"
              type="text"
              value={formik.values.phone}
              onChange={formik.handleChange}
              placeholder="Ejm: 974352615"
            />
            {formik.errors.phone && formik.touched.phone ? (
              <small className="text-danger">{formik.errors.phone}</small>
            ) : null}
            <label htmlFor="phone" className="form-label">
              Ingrese el telfono:
            </label>
          </div>
        </div>
      </div>

      <div className="row mb-3">
        <div className="col d-flex justify-content-between">
          <button
            className="btn btn-primary"
            type="button"
            onClick={() => SW?.previousStep()}
          >
            <i className="fa-solid fa-angle-left" /> Anterior
          </button>
          <button
            className="btn btn-primary"
            type="button"
            onClick={formik.submitForm}
          >
            Siguiente <i className="fa-solid fa-angle-right" />
          </button>
        </div>
      </div>
    </form>
  );
};

export default FormStep2ClientInfo;
