import React, { useContext, useRef, useState, useEffect } from 'react';
import ReactToPrint from 'react-to-print';
import { toast } from 'react-toastify';
import Swal from 'sweetalert2';
import NumberFormat from 'react-number-format';
import { postQuote } from '../../services/quoteservice';
import { QuoteObj } from '../../types/types';
import { getFormatedMonth } from '../../utils';
import { FormContext } from '../context/QuoteRegisterContext';
import logoAngeles from '../../assets/logo.png';
import FormPaymentList from './FormPaymentList';
import FormPrices from './FormPrices';
import { AuthContextQuote } from '../../authQuote/context/context';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { nameNumber } from '../../constants';
import { isMobile } from '../../utils/mobile';
import AngelesSorteoApolo from '../../assets/sorteo-auto-apolo.jpg';
import AngelesSorteoBolo2 from '../../assets/sorteo-auto-bolo2.jpg';

interface FormStep3PDFProps {
  SW?: any;
  isSummary?: boolean;
}

const FormStep3PDF = (props: FormStep3PDFProps) => {
  const mobile = isMobile();
  const { SW, isSummary } = props;
  /**
   * Get data to check if this is a summary or not
   */

  const [saved, setSaved] = useState(false);
  const [loadingQuotePost, setLoadingQuotePost] = useState(false);
  const authQuoteContext = useContext(AuthContextQuote);
  const formContext = useContext(FormContext);
  const {
    initialCash,
    showGarages,
    paymentsQty,
    initialCashDate,
    discount,
    finalCashDate,
    setModePDF,
    isCash,
    customer,
    selectedGarage,
    selectedBuilding,
    selectedDpto,
    resetQuote,
    setSavedQuoteId,
  } = formContext!;
  const { currentUser } = authQuoteContext!;
  const quoteRef = useRef(null);

  const buildQuoteObj = (): QuoteObj | undefined => {
    const { id: dpto_id, price } = selectedDpto!;
    const user_id = currentUser?.id;
    const now = new Date();
    const date = `${now.getFullYear()}-${now.getMonth() + 1}-${now.getDate()}`;
    const type = isCash ? 'contado' : 'hipotecario';
    const interested = 2;
    const initialAmount = initialCash;

    if (dpto_id && user_id) {
      return {
        discount,
        dpto_id,
        user_id,
        garage_id: selectedGarage ? selectedGarage.id : null,
        date,
        type,
        interested,
        initialAmount,
        paymentsQTY: isCash ? paymentsQty : 0,
        firstPaymentDate: isCash ? initialCashDate : null,
        lastPaymentDate: isCash ? finalCashDate : null,
        dptoPrice: price,
      };
    }
  };
  let sorteo;
  if (selectedBuilding) {
    if (selectedBuilding.name.toLowerCase().includes('apolo')) {
      sorteo = AngelesSorteoApolo;
    } else if (selectedBuilding.name.toLowerCase().includes('bolognesi')) {
      sorteo = AngelesSorteoBolo2;
    }
  }

  const handleSave = async () => {
    const { isConfirmed } = await Swal.fire({
      title: 'Guardar la cotizacin en la base de datos?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'S, guardar!',
      cancelButtonText: 'Cancelar',
    });
    if (isConfirmed) {
      const objQuote = buildQuoteObj();
      if (objQuote && customer) {
        setLoadingQuotePost(true);
        const response = await postQuote({
          quote: objQuote,
          customer,
        });
        setLoadingQuotePost(false);
        if (response.ok) {
          toast('Datos guardados correctamente!', { type: 'success' });
          setSavedQuoteId(response.content.id!);
          setSaved(true);
        } else {
          Swal.fire('Ups!', response.content, 'error');
        }
      }
    }
  };
  useEffect(() => {
    if (isSummary) {
      setSaved(true);
    }
  }, [isSummary]);

  return (
    <div className="container">
      <div className="row mb-3">
        <div className="col d-flex justify-content-between">
          <button
            className="btn btn-primary"
            type="button"
            onClick={() => {
              setModePDF(false);
              SW.previousStep();
            }}
          >
            <i className="fa-solid fa-angle-left" /> Anterior
          </button>
          <button
            disabled={!saved}
            className="btn btn-primary"
            type="button"
            onClick={() => SW?.nextStep()}
          >
            Siguiente <i className="fa-solid fa-angle-right" />
          </button>
        </div>
      </div>
      <div className="row mb-3">
        <div className="col d-flex justify-content-between">
          <div className="d-flex">
            <button
              type="button"
              className="btn btn-primary me-2"
              disabled={saved}
              onClick={handleSave}
            >
              <i className="fa-solid fa-floppy-disk me-1" />
              Guardar
            </button>

            <ReactToPrint
              pageStyle="padding: 50px"
              // eslint-disable-next-line react/no-unstable-nested-components
              trigger={() => (
                <button
                  type="button"
                  className="btn btn-primary me-2"
                  disabled={!saved}
                >
                  <i className="fa-solid fa-print me-1" />
                  Imprimir
                </button>
              )}
              content={() => quoteRef.current}
            />
          </div>

          {!isSummary && (
            <div className="d-flex">
              <button
                type="button"
                className="btn btn-outline-danger"
                onClick={() => {
                  resetQuote();
                  setSaved(false);
                  SW.goToStep(1);
                }}
              >
                {saved ? (
                  <i className="fa-solid fa-flag me-1" />
                ) : (
                  <i className="fas fa-trash me-1" />
                )}
                {saved ? 'Finalizar' : 'Cancelar'} Cotizacin
              </button>
            </div>
          )}
        </div>
      </div>
      {loadingQuotePost ? (
        <LoadingComponent
          title="Creando la cotizacin"
          message="Estamos guardando los datos en los servidores de Angeles"
        />
      ) : (
        <div className="row p-6" ref={quoteRef}>
          <div className="col">
            <div className="row">
              <div
                className={`${
                  mobile ? 'col-4' : 'col-sm-4'
                } d-flex justify-content-center align-items-center`}
              >
                <img
                  src={logoAngeles}
                  style={{ maxWidth: '150px', maxHeight: '150px' }}
                  alt="logo"
                />
              </div>
              <div
                className={`${
                  mobile ? 'col-4' : 'col-sm-4'
                } d-flex justify-content-center align-items-center`}
              >
                <h1 className="display-4 text-primary">Cotizacin</h1>
              </div>
              <div
                className={`${
                  mobile ? 'col-4' : 'col-sm-4'
                } d-flex justify-content-center align-items-center`}
              >
                <img
                  src={selectedBuilding?.logoUrl}
                  style={{ maxWidth: '150px', maxHeight: '150px' }}
                  alt="logo"
                />
              </div>
            </div>
            <div className="row mt-3">
              <div className="col-12 ">
                <table className="table table-sm table-bordered">
                  <thead>
                    <tr>
                      <th colSpan={4} className="text-center">
                        DATOS DEL CLIENTE
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        <strong>{`Sr(a/ita/es): `}</strong>
                      </td>
                      <td>{customer?.fullname}</td>
                      <td>
                        <strong>Fecha:</strong>
                      </td>
                      <td>{getFormatedMonth(new Date())}</td>
                    </tr>
                    <tr>
                      <td>
                        <strong>Telfono:</strong>
                      </td>
                      <td>{customer?.phone}</td>
                      <td>
                        <strong>Email:</strong>
                      </td>
                      <td>{customer?.email}</td>
                    </tr>
                    <tr>
                      <td colSpan={4} className="text-center">
                        <strong>Validez:</strong> <span>7 das</span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div className="row">
              <div className="col ">
                <p>
                  Gracias por contactarse con nosotros, le otorgamos la
                  cotizacin solicitada.
                </p>
              </div>
            </div>
            <div className="row">
              <div className={mobile ? 'col-6' : 'col-sm-6'}>
                <div className="table-responsive">
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr>
                        <th className="text-center" colSpan={2}>
                          DATOS DE LA PROPIEDAD
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>
                          <strong>Proyecto:</strong>
                        </td>
                        <td align="center">{selectedBuilding?.name}</td>
                      </tr>
                      <tr>
                        <td>
                          <strong>Piso:</strong>
                        </td>
                        <td align="center">
                          {selectedDpto?.floor}
                          {` (${nameNumber[selectedDpto?.floor || 0]})`}
                        </td>
                      </tr>
                      <tr>
                        <td>
                          <strong>Metraje:</strong>
                        </td>
                        <td align="center">
                          {selectedDpto?.typology?.area} m<sup>2</sup>
                        </td>
                      </tr>
                      <tr>
                        <td>
                          <strong>Tipologa</strong>
                        </td>
                        <td align="center">{selectedDpto?.typology?.name}</td>
                      </tr>
                      {showGarages && selectedGarage && (
                        <tr>
                          <td>
                            <strong>Cochera:</strong>
                          </td>
                          <td align="center">{selectedGarage.name}</td>
                        </tr>
                      )}
                      <tr>
                        <td>
                          <strong>Modalidad de compra:</strong>
                        </td>
                        <td align="center">
                          {isCash ? 'Al contado' : 'Crdito hipotecario'}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
              <div className={mobile ? 'col-3' : 'col-sm-3'}>
                <div className="row">
                  <div className="col">
                    <div className="table-responsive">
                      <table className="table table-sm table-bordered">
                        <thead>
                          <tr>
                            <th className="text-center">Precio Dpto.:</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td className="text-center">
                              <NumberFormat
                                value={selectedDpto?.price}
                                displayType="text"
                                thousandSeparator
                                prefix="S/ "
                              />
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
                {selectedGarage && showGarages ? (
                  <div className="row">
                    <div className="col">
                      <div className="table-responsive">
                        <table className="table table-sm table-bordered">
                          <thead>
                            <tr>
                              <th className="text-center">Precio Cochera:</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td className="text-center">
                                <NumberFormat
                                  value={selectedGarage?.price}
                                  displayType="text"
                                  thousandSeparator
                                  prefix="S/ "
                                />
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                ) : null}
                <div className="row">
                  <div className="col">
                    <div className="table-responsive">
                      <table className="table table-sm table-bordered">
                        <thead>
                          <tr>
                            <th className="text-center">Total:</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td className="text-center">
                              <NumberFormat
                                value={
                                  (selectedDpto?.price || 0) +
                                  (selectedGarage?.price || 0)
                                }
                                displayType="text"
                                thousandSeparator
                                prefix="S/ "
                              />
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <div className={mobile ? 'col-3' : 'col-sm-3'}>
                <div className="table-responsive">
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr>
                        <th className="text-center">CARACTERSTICAS</th>
                      </tr>
                    </thead>
                    <tbody>
                      {selectedBuilding?.services.map((s) => (
                        <tr key={s.id}>
                          <td className="text-center">* {s.name}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col">
                <div className="table-responsive">
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr>
                        <th
                          colSpan={showGarages && selectedGarage ? 1 : 2}
                          className="text-center"
                        >
                          TIPOLOGA
                        </th>
                        {showGarages && selectedGarage ? (
                          <th className="text-center">COCHERA</th>
                        ) : (
                          <th className="text-center">PROMOCIN</th>
                        )}
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td
                          align="center"
                          colSpan={showGarages && selectedGarage ? 1 : 2}
                        >
                          <img
                            src={selectedDpto?.typology?.url}
                            style={{ maxHeight: '395px' }}
                            alt="tipologa"
                          />
                        </td>
                        {showGarages && selectedGarage ? (
                          <td align="center">
                            <img
                              src={selectedGarage?.url}
                              style={{ maxHeight: '395px' }}
                              alt="imagen de garage"
                            />
                          </td>
                        ) : (
                          <td align="center">
                            {sorteo ? (
                              <img
                                src={sorteo}
                                style={{
                                  maxHeight: '395px',
                                  maxWidth: '100%',
                                }}
                                alt="imagen de promocion"
                              />
                            ) : (
                              <img
                                src={logoAngeles}
                                style={{
                                  maxHeight: '395px',
                                  maxWidth: '100%',
                                }}
                                alt="imagen de promocion"
                              />
                            )}
                          </td>
                        )}
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col ">
                <FormPrices />
              </div>
            </div>
            <div className="row">
              <div className="col ">
                <FormPaymentList />
              </div>
            </div>

            <div className="row justify-content-center">
              <div className={mobile ? 'col-6' : 'col-sm-6'}>
                <table className="table table-bordered">
                  <thead>
                    <tr>
                      <th colSpan={2} className="text-center">
                        DATOS DEL VENDEDOR
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        <strong>Nombre:</strong>
                      </td>
                      <td>{currentUser?.fullname}</td>
                    </tr>
                    <tr>
                      <td>
                        <strong>Telfono:</strong>
                      </td>
                      <td>{currentUser?.phone}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default FormStep3PDF;
