import React from 'react';
import { HashRouter, Route, Routes } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import PageLogin from '../quote/pages/PageLogin';
import PageQuoteRegister from '../quote/pages/PageQuoteRegister';
import PageHome from '../user/pages/PageHome';
import { routesCollection } from './contract';
import AuthStateQuote from '../authQuote/context/AuthStateQuote';
import PageQuoteList from '../quote/pages/PageQuoteList';
import HeaderQuote from '../shared/components/HeaderQuote';
import PageQuoteSummary from '../quote/pages/PageQuoteSummary';

const RoutesComponent = () => (
  <HashRouter>
    <ToastContainer
      position="top-right"
      autoClose={4000}
      hideProgressBar={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
      pauseOnHover
    />
    <Routes>
      <Route path="/" element={<PageHome />} />
      <Route path={routesCollection.LOGIN} element={<PageLogin />} />
      <Route
        path={routesCollection.QUOTE_REGISTER}
        element={
          <AuthStateQuote>
            <PageQuoteRegister />
          </AuthStateQuote>
        }
      />
      <Route
        path={routesCollection.QUOTE_LIST}
        element={
          <AuthStateQuote>
            <HeaderQuote />
            <PageQuoteList />
          </AuthStateQuote>
        }
      />
      <Route
        path={routesCollection.QUOTE_SUMMARY}
        element={
          <AuthStateQuote>
            <HeaderQuote />
            <PageQuoteSummary />
          </AuthStateQuote>
        }
      />
      <Route path="*" element={<div>not found</div>} />
    </Routes>
  </HashRouter>
);

export default RoutesComponent;
